import cache from '../../static/storage.js'
export default {
	data() {
		return {
			optionText: [{
				id: 0,
				text: '省'
			}, {
				id: 1,
				text: '市'
			}, {
				id: 2,
				text: '区'
			}],
			location: '',
			opnum: 0,
			opinfo: [],
			oparr: [],
			oparrA: [],
			oparrB: [],
			oparrC: [],
			region: [],
			showcp: false,
			id: '',
			swiper: [],
			showswiper: [],
			color: [],
			img: [],
			textinfo: {},
			oldclr: '',
			num: 0
		};
	},
	methods: {
		inshop() {
			uni.switchTab({
				url: '/pages/shopcart/shopcart'
			});
		},
		addshop(img, id, obj) {
			let bool = Boolean(cache.getlocation()),
				data = {
					...obj,
					img,
					id
				}
			
			if (bool) {
				cache.addshop(data)
				this.num++
			} else {
				uni.showToast({
					title: "请设置地址",
					duration: 3000,
					icon: "none"
				});
			}
		},
		changeOp(id) {
			this.opnum = id
			switch (id) {
				case 0:
					this.oparr = this.oparrA
					break;
				case 1:
					this.oparr = this.oparrB
					break;
				case 2:
					this.oparr = this.oparrC
					break;
				default:
					break;
			}
		},
		changeClr(clr) {
			uni.redirectTo({
				url: `/pages/shopdetails/shopdetails?id=${this.id}&clr=${clr}`
			});
		},
		showpopup() {
			this.showcp = true
			if (!this.opinfo.length) {
				this.oparr = this.region.map(i => {
					return {
						value: i.value,
						label: i.label
					}
				})
				this.oparrA = this.oparr
			}
		},
		nextop(la, va) {
			switch (this.opnum) {
				case 0:
					this.opnum++
					this.opinfo = [{
						label: la,
						value: va
					}]
					this.oparr = this.region.filter(i => {
						return i.label === la
					})[0].children.map(i => {
						return {
							label: i.label,
							value: i.value
						}
					})
					this.oparrB = this.oparr
					this.oparrC = []
					break;
				case 1:
					this.opnum++
					this.opinfo.splice(1)
					this.opinfo.push({
						label: la,
						value: va
					})
					this.oparr = this.region.filter(i => {
						return i.label === this.opinfo[0].label
					})[0].children.filter(i => {
						return i.label === la
					})[0].children
					this.oparrC = this.oparr
					break;
				case 2:
					this.opinfo.splice(2)
					this.opinfo.push({
						label: la,
						value: va
					})
					this.location = this.opinfo[0].value + this.opinfo[1].value + this.opinfo[2].value
					this.showcp = false
					cache.setlocation(this.opinfo)
					break;
				default:
					break;
			}
		},
		hidepopup() {
			this.showcp = false

		}
	},
	onLoad(e) {
		let {
			clr,
			id
		} = e,
		loca = cache.getlocation()
		let shopcar = cache.getshop() || []
		
		uni.request({
			url: `https://www.easy-mock.com/mock/5cb03f83f593356878131486/wph/shopinfo/${id}`,
			success: res => {
				let {
					viewpager,
					color,
					img,
					price_1,
					price_2,
					price_3,
					text
				} = res.data.info
				this.id = id
				this.oldclr = clr
				this.swiper = viewpager
				this.color = color
				this.img = img
				this.textinfo = {
					price_1,
					price_2,
					price_3,
					text
				}
				this.showswiper = this.oldclr === '' ? this.swiper[0].arr : this.swiper.filter(i => {
					return i.clr === this.oldclr
				})[0].arr
			}
		});
		new Promise((resolve, reject)=>{
			uni.request({
				url: `https://www.easy-mock.com/mock/5cb03f83f593356878131486/region`,
				success: res => {
					this.region = res.data.data
					resolve()
				}
			})
		}).then(()=>{
			if (loca) {
				this.opnum = 2
				this.opinfo = loca
				this.oparrA = this.region.map(i => {
					return {
						value: i.value,
						label: i.label
					}
				})
				this.oparrB = this.region.filter(i => {
					return i.label === this.opinfo[0].label
				})[0].children.map(i => {
					return {
						label: i.label,
						value: i.value
					}
				})
				
				this.oparrC = this.region.filter(i => {
					return i.label === this.opinfo[0].label
				})[0].children.filter(i => {
					return i.label === this.opinfo[1].label
				})[0].children
				this.oparr = this.oparrC
				this.location = this.opinfo[0].value + this.opinfo[1].value + this.opinfo[2].value

			}
		})
		
		shopcar.length !== 0 ? this.num = shopcar.reduce((acc,i)=>{return acc + i.num},0) : this.num = 0
	},
	onShow(){
		let shopcar = cache.getshop() || []
		shopcar.length !== 0 ? this.num = shopcar.reduce((acc,i)=>{return acc + i.num},0) : this.num = 0
	}
}
